<template>
  <div id="invoice-top">
    <form
      action="javascript: searchAnimation()"
      style="display:flex;justify-content:center;margin:6px 0;"
    >
      <input
        type="search"
        results="5"
        name="word"
        id="Search"
        placeholder="请输入姓名/手机号/订单号"
        v-model="value"
        @keyup.enter="searchAnimation"
        class="input_border"
      />
      <div
        class="search"
        style="border-radius: 0 20px 20px 0;"
      >搜索</div>
    </form>
    <van-tabs
      v-model="selected"
      title-active-color='#ff7d41'
      color='#ff7d41'
      @click="chooseSelected"
    >
      <van-tab :title="'待开票 ' + '(' + orderL + ')' ">
      </van-tab>
      <van-tab :title="'已完成' + '(' + orderR + ')' ">
      </van-tab>
    </van-tabs>
    <!-- 开始 -->
    <div class="task-list">
      <van-pull-refresh
        v-model="pullLoading"
        @refresh="onPullRefresh"
        success-text="刷新成功"
      >
        <van-list
          v-model="listLoading"
          @load="onLoadList"
          :offset="30"
          :finished="finishedList"
          finished-text="没有更多了"
        >
          <div
            v-for="(item, i) in listData"
            :key="i"
            class="my_order"
          >
            <div class="per_info">
              <p style="color:#FF7D41;">
                <span v-if="item.orderRoom.order.roomUser">{{ item.orderRoom.order.roomUser}}</span>
                <span v-else>{{ item.sysUser?item.sysUser.realName:'' }}</span>
                <span
                  style="color:#707070"
                  v-if="item.orderRoom.order.phone == null || item.orderRoom.order.phone == '' "
                >{{ item.sysUser == null?'':item.sysUser.phone }}</span>
                <span
                  style="color:#707070;"
                  v-else
                  @click="callPhone(item.orderRoom.order.phone)"
                >{{ item.orderRoom.order.phone }}</span>
                <span
                  class="my-invoice"
                  v-if="item.isInvoice == 1"
                >开发票</span>
              </p>
              <p style="color:#707070">
                {{ orderSourceMap[item.orderRoom.order.orderSource] }}
                <span>{{ item.orderRoom.order.externalOrderSn!=null? item.orderRoom.order.externalOrderSn:'' }}</span>
              </p>
            </div>
            <div style="display:flex;justify-content: space-between;">
              <div style="text-align:left;line-height:30px;width:70%;width:100%;">
                <p style="color:#707070;margin-top:8px;display:flex;flex-direction:column;">
                  <span>{{ item.orderRoom.roomScale?item.orderRoom.roomScale.scaleName:'' }}</span>
                  <span>
                    共1间
                    <span v-if="item.orderRoom.roomSn==null">未办理入住</span>
                    <span v-else>房间号{{ item.orderRoom.roomSn }}</span>
                    <span
                      v-if="item.orderRoom.orderType==3 ||item.orderRoom.orderType==4"
                      class="long"
                    >{{ item.orderRoom.orderType==3?'长':'短' }}租</span>
                  </span>
                </p>
                <p v-if="item.orderRoom.orderType==3">租期：{{item.orderRoom.order.startTime | dataFilter}} 至 {{item.orderRoom.order.endTime | dataFilter}}</p>
                <p v-if="item.orderRoom.orderType==3">共{{((item.orderRoom.order.endTime.substr(0,4))-(item.orderRoom.order.startTime.substr(0,4)))*12+((item.orderRoom.order.endTime.substr(5,2)-item.orderRoom.order.startTime.substr(5,2)))}}月</p>
                <p v-if="item.orderRoom.orderType==3">
                  <span>{{payments[item.order.paymentMethod]}}</span>&emsp;
                  <span>&yen;{{item.orderRoom.order.price?item.orderRoom.order.price:'0'}}/月</span>
                </p>
                <p
                  style="color:#000;"
                  v-if="item.orderRoom.orderType!=3"
                >{{item.orderRoom.order.startTime | dataFilter('MM-DD')}}至{{item.orderRoom.order.endTime | dataFilter('MM-DD') }}&emsp;&emsp;共{{ item.orderRoom.dayIn }}{{item.orderRoom.orderType==2?'小时':'晚'}}</p>
              </div>
              <div
                class="info_right"
                style="display:flex;flex-direction: column;line-height:50px;"
              >
                <span
                  v-if="item.orderRoom.order.orderSource == 1"
                  style="color:#000; width: 100px; display:block; float:left; text-align:right;"
                >房费:&yen;{{ item.orderRoom.invoiceAmount }}</span>
                <span
                  v-else
                  style="color:#000; width: 50px; display:block; float:left; text-align:right;margin-bottom:30px;"
                ></span>
                <span style="display:inline-block;height:20px;color:#707070;">{{ item.statusFont }}</span>
              </div>
            </div>
            <div
              class="per_bottom"
              style="border-top:'1px solid #f6f6f6'"
            >
              <p
                class="btn_left"
                v-if="selected == 0"
                @click="goNext('/details',item)"
              >订单详情</p>
              <p
                class="btn_left"
                :style="{color:(selected == 0)?'#FF7D41':''}"
                @click="goNext('/invoice',item)"
              >{{ selected == 0?'去开票':'开票详情' }}</p>
            </div>
          </div>
        </van-list>
      </van-pull-refresh>
    </div>
    <!-- 结束 -->
  </div>
</template>
<script>
import { GetSystemDictDataByType } from '@/api/api_query';
import { GetBusinessInvoiceList } from '@/api/api_invoice';
import dayjs from 'dayjs';
export default {
    data() {
        return {
            selected: 0,
            value: null,
            listLoading: false, //  上滑列表加载（每一次上滑的时候）---加载中
            finishedList: false, //  上滑列表加载完了所有数据---没有更多了）
            pullLoading: false, // 下拉刷新加载动画---加载中
            dataForm: {
                pageSize: 10,
                pageNum: 0,
                status: null,
                hotelId: this.$route.query.hotelId,
                searchValue: this.value
            },
            listData: [],
            orderSourceMap: [],
            payments: [],
            roles: [],
            orderL: 0,
            orderR: 0,
            a: null
        };
    },
    created() {
        this.$store.commit('setHeader', '待开票订单');
        if (localStorage.getItem('roles')) {
            this.roles = JSON.parse(localStorage.getItem('roles')).map((item) => item.name);
        }
        this.a = dayjs().format('YYYY-MM-DD HH:mm:ss');
        this.getType();
    },
    watch: {
        value(neww) {
            this.dataForm.searchValue = neww;
            this.dataForm.pageNum = 0;
            this.listData = [];
            this.onLoadList();
        }
    },
    methods: {
        getType() {
            GetSystemDictDataByType({ dictType: 'business_payment_method' }).done((res) => {
                this.options = res.data.map((item) => {
                    return {
                        label: item.dictLabel,
                        value: item.dictValue
                    };
                });
                let payments = {};
                for (var i = 0; i < this.options.length; i++) {
                    payments[this.options[i].value] = this.options[i].label;
                }
                this.payments = payments;
            });
            GetSystemDictDataByType({ dictType: 'business_order_source' }).done((res) => {
                let orderSourceMap = {};
                res.data.forEach((item) => {
                    orderSourceMap[item.dictSort] = item.dictLabel;
                });
                this.orderSourceMap = orderSourceMap;
            });
        },
        // 切换
        chooseSelected() {
            this.listData = [];
            this.dataForm.pageNum = 0;
            this.onLoadList();
        },
        //  下拉刷新列表
        onPullRefresh() {
            this.listData = [];
            this.dataForm.pageNum = 0;
            this.finishedList = false;
            // this.onLoadList();
        },
        //  上滑加载列表
        onLoadList() {
            if (this.selected == 0) {
                this.dataForm.status = '1';
            } else {
                this.dataForm.status = '2';
            }
            GetBusinessInvoiceList(this.dataForm).done((res) => {
                this.listData = this.listData.concat(res.data);
                this.listLoading = this.pullLoading = false;
                if (this.selected == 0) {
                    this.orderL = res.order;
                } else {
                    this.orderR = res.order;
                }
                if (res.order == this.listData.length) {
                    this.finishedList = true;
                } else if (res.order > this.listData.length) {
                    this.finishedList = false;
                }
            });
            this.dataForm.pageNum++;
        },
        // 跳转
        goNext(url, item) {
            this.$router.push({
                path: url,
                query: {
                    orderRoomId: item.orderRoomId,
                    invoiceAmount: item.orderRoom.invoiceAmount
                }
            });
        }
    }
};
</script>
<style scoped>
/deep/ .van-cell {
    line-height: 26px;
    padding: 10px 16px 10px 0;
    color: #000;
}
.per_info {
    line-height: 50px;
    border-bottom: 1px solid #f6f6f6;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.task-list {
    overflow-y: auto;
    position: absolute;
    top: 150px;
    bottom: 0;
    width: 100%;
}
.my_order {
    background: #fff;
    padding: 10px 20px;
    font-size: 0.8rem;
    margin-bottom: 10px;
}
/* 按钮 */
.per_bottom {
    padding-top: 5px;
    display: flex;
    justify-content: flex-end;
}
.btn_left {
    color: #888888;
    font-size: 12px;
    padding: 5px 5px;
    border: 1px solid #ececec;
    border-radius: 15px;
    margin-bottom: 5px;
    width: 20%;
    margin-left: 5px;
}
.my-invoice {
    border-radius: 5px;
    font-size: 0.5rem;
    background: #ffe6e7;
    color: #fb575b;
    line-height: 20px;
    display: inline-block;
    padding: 2px 4px;
    margin-left: 4px;
}
.input_border {
    font-size: 0.7rem;
    width: 80%;
    line-height: 36px;
    border-radius: 20px 0 0 20px;
    text-indent: 2rem;
}
.search {
    font-size: 0.7rem;
    color: #fff;
    display: inline-block;
    width: 15%;
    line-height: 36px;
    background-color: #ff7d41;
}
.search:active {
    opacity: 0.5;
}
</style>



