import * as API from './';
import {
  data
} from 'autoprefixer';


/*********************************************  发票  *******************************************/
// 查询发票信息 
export const GetBusinessInvoiceByorderroom = data => API.GET(`/api/business/invoice/byorderroom/${data.orderRoomId}`, true)
// 发票开票确认 
export const GetBusinessInvoiceAccept = data => API.POST(`/api/business/invoice/accept/${data.orderInvoiceId}`, true)


/*********************************************  发票模块  *******************************************/
// 分页查询发票信息
export const GetBusinessInvoiceList = data => API.GET(`/api/business/invoice/list`,data, true)




